import { Command, Query } from '@ali/4ever-cangjie';
import toolbar from './toolbar';
import menubar from './menubar';
import toggleUnderline from './commands/toggleUnderlineMark';
import isUnderline from './queries/isUnderline';
import * as actions from './actions';
import onAction from './handlers/onAction';
import onKeyDown from './handlers/onKeyDown';
import createOnCangjieInput from './handlers/onCangjieInput';
import createRenderToolbar from './createRenderToolbar';
import { moFactory } from '@ali/4ever-plugin-underline';

const commands: Record<string, Command> = {
  toggleUnderline,
};
const queries: Record<string, Query> = {
  isUnderline,
};

function createUnderlinePlugin(config) {
  const underlineConfig = config.underline || {};
  return {
    ...moFactory({}),
    menubar: menubar(),
    toolbar: toolbar(underlineConfig),
    renderToolbar: createRenderToolbar(underlineConfig.locale),
    commands,
    queries,
    actions,
    onAction,
    onKeyDown,
    onCangjieInput: createOnCangjieInput(),
    models: {
      klass: 'mark',
      type: 'underline',
    },
  };
}

createUnderlinePlugin.actions = actions;
createUnderlinePlugin.commands = commands;
createUnderlinePlugin.queries = queries;

export default createUnderlinePlugin;
